import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import Nav from './Nav';
import Axios from 'axios';
import { v1 as uuid } from "uuid";
import {useHistory} from 'react-router-dom';
import './style.css';
import Noty from 'noty';
import '../../node_modules/noty/src/noty.scss';
import '../../node_modules/noty/src/themes/mint.scss';
import './not.scss';
function Register() {
    const history = useHistory();
    const [loading, setloading] = useState(false);
    
    const id = uuid();
    const onsubmit = async (e) => {
        e.preventDefault();
    if(!e.target.name.value || !e.target.pass.value || !e.target.cpass.value) {
      alert('All fields are required !')
    }
    if(e.target.pass.value !== e.target.cpass.value){
        alert('Password does not match !')
    }
        
        
        const info = {
            name: e.target.name.value,
            password: e.target.pass.value,
            uuid: id
        }
        setloading(true);
      await Axios.post('https://immense-beach-20257.herokuapp.com/register',info).then(res => {
         
         setloading(false);
         if(res.data.user !== "0") {
        //    props.setcustomer(res.data.user);
          
        //   props.setLogin("true");
          history.push('/login');
          new Noty({
            text: 'Registered in successfully!',
            progressBar: false,
            timeout: 1400,
            type: "success"
          }).show()
         }
         else{
           setloading(false);
          history.push('/register');
          new Noty({
            text: "This Username is already exist !",
            progressBar: false,
            timeout: 1400,
            type: "error"
          }).show()
         }
       })

    }
    return (
      <>
<Nav />
<section className="login flex justify-center py-24">
        <div className="w-full max-w-xs">
            <form onSubmit={onsubmit} className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
              <div className="mb-4">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="username">
                  Username
                </label>
                <input name="name" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Unique  UserName" />
              </div>
             
              <div className="mb-6">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="password">
                  Password
                </label>
                <input name="pass" className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************" />
              
              </div>
              <div className="mb-6">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="password">
                 Confirm Password
                </label>
                <input name="cpass" className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************" />
              
              </div>
              <div className="flex items-center justify-between">
                <button disabled={loading} type="submit" className="authbtn rounded-full text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">
                  SignUp
                </button>
                <Link className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" to="/login">
                  Already have Accoumt?
                </Link>
              </div>
            </form>
            <p className="text-center text-gray-500 text-xs">
              &copy;2020 Pizza Hut. All rights reserved.
            </p>
          </div>
    
    </section>
      </>
    )
}

export default Register
