import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import Nav from './Nav';
import Axios from 'axios';
import {setLogin,setUrl} from '../store/actioncreator';
import {useHistory} from 'react-router-dom';
import Noty from 'noty';
import '../../node_modules/noty/src/noty.scss';
import '../../node_modules/noty/src/themes/mint.scss';
import './not.scss';
import './style.css';
import {connect} from 'react-redux';
function Login(props) {
    const history = useHistory();
    const [loading, setloading] = useState(false);
    const onsubmit = async (e) => {
        e.preventDefault();
        if(!e.target.name.value || !e.target.pass.value) {
          alert('All fields are required !')
        }
        const info = {
            name: e.target.name.value,
            password: e.target.pass.value,

        }
        setloading(true);
      await Axios.post('https://immense-beach-20257.herokuapp.com/login',info).then(res => {
         
         setloading(false);
         if(res.data.user !== "0") {
        //    props.setcustomer(res.data.user);
          
        //   props.setLogin("true");
      const userurl = res.data.user.uuid;
        props.setUrl(userurl);
        props.setLogin('true');
        
          history.push('/');
         
          new Noty({
            text: 'Logged in successfully!',
            progressBar: false,
            timeout: 1400,
            type: "error"
          }).show()
         }
         else{
           setloading(false);
          history.push('/login');
          new Noty({
            text: "User done not exist !",
            progressBar: false,
            timeout: 1400,
            type: "error"
          }).show()
         }
       })
    }
    return (
     <>
     <Nav />
<section className="login flex justify-center py-24">
        <div className="w-full max-w-xs">
            <form onSubmit={onsubmit} className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
              <div className="mb-4">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="username">
                  Username
                </label>
                <input name="name" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter your Username" />
              </div>
              <div className="mb-6">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="password">
                  Password
                </label>
                <input name="pass" className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************" />
              
              </div>
              <div className="flex items-center justify-between">
                <button disabled={loading} className="authbtn rounded-full text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="submit">
                  Login
                </button>
                <Link className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" to="/register">
                  Don't have Account?
                </Link>
              </div>
            </form>
            <p className="text-center text-gray-500 text-xs">
              &copy;2020 Pizza Hut. All rights reserved.
            </p>
          </div>
    
    </section>

     </>
    )
}
const mapDispatchToProps = (dispatch) => {
    return {
        setLogin: (bool) => {
            dispatch(setLogin(bool))
        },
        setUrl: (bool) => {
          dispatch(setUrl(bool))
      }

    }
}
const mapStateToProps = (state) => {
    return {
        isLog: state.isLog
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Login)
