import {Login,Logout,Url} from './actiontype';
const initstate = {
    isLog: 'false',
    myurl: 'http://localhost:3000/'
};
export const blogreducer = (state = initstate,action) => {
    if(action.type === Login){
        return{
            ...state,
          isLog: 'true'
        }
    }
    else if(action.type === Logout){
        return{
            ...state,
            isLog: 'false'
        }
    }
    else if(action.type === Url){
        const newurl = state.myurl + action.url;
        return{
            ...state,
            myurl: newurl
        }
    }
    else{
        return{
            ...state
        }
    }
    

}