import {Login,Logout,Url} from './actiontype';
export const setLogin = (bool) => {
    return{
        type: Login
    }
}
export const setLogout = (bool) => {
    return{
        type: Logout
    }
}
export const setUrl = (bool) => {
    return{
        type: Url,
        url: bool
    }
}