import React, { useState } from "react";
import { v1 as uuid } from "uuid";
import {useHistory} from 'react-router-dom';
import './style.css';
import {Link} from 'react-router-dom';
import Nav from './Nav';
import i2 from './i2.png';
import {Modal,Button } from "react-bootstrap";
const CreateRoom = (props) => {
  const [modalShow, setmodalShow] = useState(false);
        const id = uuid();
        // props.history.push(`/room/${id}`);
    
const history = useHistory();
    return (
        <>
        <Nav />
      {/* <div className="container overflow-hidden">
      <Button className="butn my-4" onClick={() => {setdis(true)}}>Create room</Button>
        {dis && <div>
            <h4 className="my-3 text"> Share Url : <Link onClick={() => {history.push(`/room/${id}`)}}><br />{`http://localhost:3000/room/${id}`}</Link></h4>
        </div>}
      </div> */}

      <div className=' container mycontainer flex mysec'>
       <div className="col-12 col-lg-4">
         <h2> Let's Meet</h2>
         <h3>With Friedns!</h3>
         <p> You can make your own groups and chat with new friends over the world !</p>
       {/* {props.isLog === 'false' && <Link to='/login' className="ab">Let's Connect</Link>} */}
       <Link onClick={() => {setmodalShow(true)}} className="ab">New Meeting</Link>
       </div>
       <div className="col-12 col-md-6">
         <img src={i2} alt="pic" />
       </div>
     </div>

     <Modal
      {...props}
      show={modalShow}
      onHide={() => {setmodalShow(false)}}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          share this URL 
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h6> <Link onClick={() => {history.push(`/room/${id}`)}}> {`https://anku1903.github.io/call-webrtc/room/${id}`} </Link> </h6>
        
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => {setmodalShow(false)}}>Close</Button>
      </Modal.Footer>
    </Modal>
    



        </>
    );
};

export default CreateRoom;
