
import React, { useEffect, useRef, useState } from "react";
import io from "socket.io-client";
import Peer from "simple-peer";
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../../node_modules/bootstrap/dist/js/bootstrap.min.js';
import styled from "styled-components";
import './style.css';
import {useHistory} from 'react-router-dom';
import VideocamIcon from '@material-ui/icons/Videocam';
import MicIcon from '@material-ui/icons/Mic';
import MicOffIcon from '@material-ui/icons/MicOff';
import VideocamOffIcon from '@material-ui/icons/VideocamOff';
import CallEndIcon from '@material-ui/icons/CallEnd';
import IconButton from '@material-ui/core/IconButton';


const StyledVideo = styled.video`
    height: 50vh !important;
    width: 50% !important;
    margin: 0;
`;

const Video = (props) => {
    const ref = useRef();

    useEffect(() => {
        props.peer.on("stream", stream => {
            ref.current.srcObject = stream;
        })
    }, []);

    return (
        <StyledVideo playsInline autoPlay ref={ref} />
    );
}


const videoConstraints = {
    height: window.innerHeight / 2,
    width: window.innerWidth / 2
};

const Room = (props) => {
    const [peers, setPeers] = useState([]);
    const socketRef = useRef();
    const userVideo = useRef();
    const peersRef = useRef([]);
    const history = useHistory();
    const roomID = props.match.params.roomID;
    const [videoon, setvideoon] = useState(true);
    const [audioon, setaudioon] = useState(false);
    const mystream = useRef(new MediaStream());
    const vstream = useRef();
    const astream = useRef();
    
    useEffect(() => {
        socketRef.current = io.connect("https://immense-beach-20257.herokuapp.com/");
        navigator.mediaDevices.getUserMedia({ video: videoConstraints, audio: true }).then(stream => {
            mystream.current = stream;
        vstream.current = stream.getVideoTracks()[0];
        
        astream.current = stream.getAudioTracks()[0];
        astream.current.enabled = false;
            userVideo.current.srcObject = stream;
            socketRef.current.emit("join room", roomID);
            socketRef.current.on("all users", users => {
                const peers = [];
                users.forEach(userID => {
                    const peer = createPeer(userID, socketRef.current.id, stream);
                    peersRef.current.push({
                        peerID: userID,
                        peer,
                    })
                    peers.push(peer);
                })
                setPeers(peers);
            })

            socketRef.current.on("user joined", payload => {
                const peer = addPeer(payload.signal, payload.callerID, stream);
                peersRef.current.push({
                    peerID: payload.callerID,
                    peer,
                })

                setPeers(users => [...users, peer]);
                console.log(peers);
            });

            socketRef.current.on("receiving returned signal", payload => {
                const item = peersRef.current.find(p => p.peerID === payload.id);
                item.peer.signal(payload.signal);
            });
        })
    }, []);
const Mute = () => {
    const enable = astream.current.enabled;
    if(enable) {
        astream.current.enabled = false;
    }
    else{
     
       astream.current.enabled = true;   
    }
}
const showhide = () => {
    const enable = vstream.current.enabled;
    if(enable) {
        vstream.current.enabled = false;
    }
    else{
     
       vstream.current.enabled = true;   
    }
}
    function createPeer(userToSignal, callerID, stream) {
        const peer = new Peer({
            initiator: true,
            trickle: false,
            stream,
        });

        peer.on("signal", signal => {
            socketRef.current.emit("sending signal", { userToSignal, callerID, signal })
        })

        return peer;
    }

    function addPeer(incomingSignal, callerID, stream) {
        const peer = new Peer({
            initiator: false,
            trickle: false,
            stream,
        })

        peer.on("signal", signal => {
            socketRef.current.emit("returning signal", { signal, callerID })
        })

        peer.signal(incomingSignal);

        return peer;
    }

    return (
        <div className="cont">    
        <div className="row p-0 m-0">    
        <StyledVideo muted ref={userVideo} className="m-0 p-0" autoPlay playsInline />
            {peers.map((peer, index) => {
                return (
                    <Video key={index} peer={peer} />
                );
            })}
            
                <div className='row down mx-auto'>
                    <div className="col">
                    <IconButton onClick={() => {
                        setvideoon(!videoon);
                        showhide()
                        }} color="primary" aria-label="video" component="span">
          {videoon && <VideocamIcon />}
          {!videoon && <VideocamOffIcon />}

                    </IconButton>
                    </div>
                    <div className="col">
                    <IconButton onClick={() => {history.push('/')}} color="primary" className="endcall" aria-label="video" component="span">
          <CallEndIcon />
                    </IconButton>
                    </div>
                    <div className="col">
                    <IconButton onClick={() => {setaudioon(!audioon);
                    Mute()
                    }} color="primary" aria-label="video" component="span">
          {audioon && <MicIcon />}
          {!audioon && <MicOffIcon />}
                    </IconButton>
                    </div>
                </div>
</div>
            </div>
        
    );
};

export default Room;