import React,{useEffect,useState} from 'react'
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {setLogout} from '../store/actioncreator';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import {Modal,Button} from 'react-bootstrap';
import '../../node_modules/bootstrap/dist/js/bootstrap.min.js';
import './style.css';
import {useHistory} from 'react-router-dom';
function Nav(props) {
    const [modalShow, setModalShow] = useState(false);
   
    const history = useHistory();
    useEffect(() => {
        const burg = document.querySelector('.burger');
    const navlinks = document.querySelector('.links');
    const alllink = document.querySelectorAll('.links li');
    burg.addEventListener('click',() => {
navlinks.classList.toggle('open');
alllink.forEach(link => {
    link.classList.toggle('fade');
})
    })
        
    }, [])
   if(props.isLog === 'false') {
    return (
        <>
        <nav>

<div className="burger">
    <div className="line"></div> 
    <div className="line"></div>   
    <div className="line"></div>
</div>


<ul className="links">
<li><Link to="/" className="a">Home</Link></li>
<li><Link to="/login" className="a">Login</Link></li>
<li><Link to="/register" className="a">Register</Link></li>
</ul>


    </nav>
  
        </>
    )
   }
   else{
    return (
        <>
        <nav>

<div className="burger">
    <div className="line"></div> 
    <div className="line"></div>   
    <div className="line"></div>
</div>

<ul className="links">
<li><Link to="/" className="a">Home</Link></li>
<li><Link to="/users" className="a">Users</Link></li>
<li><Link className="a" onClick={() => {setModalShow(true)}}>Your Url</Link></li>
<li><Link to="/" className="a" onClick={() =>{props.setLogout('true')}}>Logout</Link></li>
</ul>



    </nav>
    <Modal
         show={modalShow}
        onHide={() => setModalShow(false)}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title className="mx-auto" id="contained-modal-title-vcenter">
            Your Default Url
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="mx-auto">
     <h5> <Link onClick={() => {history.push(`/room/${props.myurl}`)}}>{props.myurl}</Link>  </h5>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={() => {setModalShow(false)}}>Cancel</Button>
        </Modal.Footer>
      </Modal>
        </>
    )
   }
}
const mapStateToProps = (state) => {
    return {
        isLog: state.isLog,
        
        myurl: state.myurl
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setLogout: (bool) => {
            dispatch(setLogout(bool))
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Nav)
